export default {
  name: "App",
  data () {
    return {
      list: wordList,
      oldListNum: JSON.parse(
        localStorage.array || JSON.stringify({ oldListNum: [], newListNum: [] })
      ).oldListNum,
      newListNum: JSON.parse(
        localStorage.array || JSON.stringify({ oldListNum: [], newListNum: [] })
      ).newListNum,
      showId:
        JSON.parse(
          localStorage.details ||
            JSON.stringify({ old: 0, new: 0, time: 0, showId: 0 })
        )?.showId || 0,
      currentEnglish:'bride',
      currentWordSex:'n.',
      currentChinese:'',
      showChinese: false,
      oldDate: 0,
      timer: '',
      timer2: '',
      details: JSON.parse(
        localStorage.details ||
          JSON.stringify({ old: 0, new: 0, time: 0, showId: 0 })
      )
    }
  },
  props: [],
  components: {},
  watch: {
    showId () {
      const myDate = new Date()
      this.details.time = myDate.getMinutes() - this.oldDate
      localStorage.details = JSON.stringify(this.details)
      localStorage.array = JSON.stringify({
        oldListNum: this.oldListNum,
        newListNum: this.newListNum
      })
    }
  },
  computed: {},
  created () {
    
    const myDate = new Date()
    this.oldDate = myDate.getMinutes()
    this.timer = setTimeout(() => {
      this.showChinese = true
    }, 5000)
  },
  methods: {
    preWord () {
      clearTimeout(this.timer)
      this.showChinese = false
      this.timer = setTimeout(() => {
        this.showChinese = true
      }, 5000)
      if (this.newListNum.indexOf(this.showId) === -1) {
        this.details.new++
        this.newListNum.push(this.showId)
      }
      this.showId > 0 ? this.showId-- : (this.showId = 0)
      this.details.showId = this.showId
      this.currentEnglish = this.list[this.showId].english
      this.currentWordSex = this.list[this.showId].wordSex
      this.currentChinese = this.list[this.showId].chinese
    },
    nextWord () {
      clearTimeout(this.timer)
      this.showChinese = false
      this.timer = setTimeout(() => {
        this.showChinese = true
      }, 5000)
      if (this.newListNum.indexOf(this.showId) === -1) {
        this.details.new++
        this.newListNum.push(this.showId)
      }
      this.showId < this.list.length - 1
        ? this.showId++
        : (this.showId = this.list.length - 1)
      this.details.showId = this.showId
      this.currentEnglish = this.list[this.showId].english
      this.currentWordSex = this.list[this.showId].wordSex
      this.currentChinese = this.list[this.showId].chinese

    },
    handleLoading () {
      this.showChinese = true
    }
  }
  
}
